import React, { useState } from "react";

const Form = ({ handleSubmit, history }) => {
  const [searchEntry, setSearchEntry] = useState("");
  const updateSearchInput = e => {
    e.preventDefault();
    setSearchEntry(e.target.value);
    e.target.placeholder = e.target.value;
    let target = e.target.placeholder;
    document.getElementById("input-text").innerHTML = target;
    // handleSubmit(e, history, searchEntry)
  };


  function keyEnter(e) {
    if (e.key === "Enter")
      alert(1)
  }


  // function keyEnter(event) {
  //   const input = document.getElementsByClassName("search-form");
  //   input.addEventListener("keypress", function (event) {
  //     if (event.key === "Enter") {
  //       event.preventDefault();
  //       alert(1)
  //     }
  //   }
  // }

  return (
    <>
      <form
        className="search-form"
        onSubmit={e => handleSubmit(e, history, searchEntry)}
        //onMouseEnter={e => handleSubmit(e, history, searchEntry)}
        onChange={e => handleSubmit(e, history, searchEntry)}
        onMouseOut={e => handleSubmit(e, history, searchEntry)}
        onMouseEnter={e => handleSubmit(e, history, searchEntry)}
        onToucheEnter={e => handleSubmit(e, history, searchEntry)}
        onToucheCancel={e => handleSubmit(e, history, searchEntry)}
        onToucheStart={e => handleSubmit(e, history, searchEntry)}
        onFocusIn={e => handleSubmit(e, history, searchEntry)}
        onFocusOut={e => handleSubmit(e, history, searchEntry)}
        onToucheMove={e => handleSubmit(e, history, searchEntry)} onkeypress={e => keyEnter(e)}
      >
        <input type="text" name="search"
          onChange={updateSearchInput} placeholder="Voer hier uw zoekwoord in"
          value={searchEntry}
        />
        <button type="submit"></button>
      </form>
      <h1 id="input-text" className="input-text">{updateSearchInput}</h1>
    </>
  );
};

export default Form;
